html,
body,
* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  box-sizing: border-box;
}

.app {
  min-height: 100vh;
  height: 100%;
}

html,
body,
.app {
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  max-width: 1366px;
  margin: 0 auto;
  background: white;
  font-family: 'Roboto Slab', serif;
  h1 {
    font-family: 'Baloo Bhai', cursive;
  }
  ul {
    list-style: none;
  }
}
